<template>
	<view class="body">
		<view class="content">
			<view class="produce">
				<view class="loction">{{data.get_school.place || "未知"}}：{{data.get_school.name || "未知"}}-{{data.get_school.type || "未知"}}-{{data.get_school.properties || "未知"}}</view>
				<view class="link" @click="toLink">链接：信息来源</view>
				<view class="num">招生人数：{{data.num || 0}}人</view>
				<view class="contact">联系方式：{{data.phone || "未知"}}</view>
			</view>
			<view class="moudle" v-html="data.content"></view>
		</view>
		<!-- 底部部分 -->
		<view :class="{bottom:true, cellectNone:status==1}">
			<view class="erroIcon" @click="toError">调剂报错</view>
			<view class="collectModlue" v-if="status==0">
				<view :class="{school:true, cellected:data.re_col_sch==true}" @click="cellectSchool" v-if="data.school_id!=0">
					<image src="../../static/index/collectList_icon.png" mode="widthFix"></image>
					<view>订阅学校</view>
				</view>
				<view :class="{major:true, cellected:data.re_col_major==true}" @click="cellectMajor" v-show="data.major_id!=0">
					<image src="../../static/index/collect_icon.png" mode="widthFix"></image>
					<view>订阅专业</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		see_data, chartcol
	} from '@/common/http.api.js';
	export default{
		data() {
			return {
				token:'',
				id:'',
				data:{},
				status:0,
			}
		},
		onLoad(option) {
			this.token = store.state.vuex_token
			this.status = option.status
			this.id = option.id
			console.log(this.status)
			see_data({token:this.token,id:option.id}).then(res=>{
				console.log(res)
				if(res.data.code==-1){
					this.$u.toast(res.data.data)
				}
				this.data = res.data.data
			}).catch(err=>{
				console.log(err)
			})
		},
		methods:{
			// 订阅学校
			cellectSchool() {
				let states = 1
				this.data.col == null ? states=1 : states=0
				chartcol({token:this.token, id:this.data.school_id, state:states, type:'school'}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}
					if(states==1){
						this.$u.toast('收藏成功')
					}else{
						this.$u.toast('取消收藏成功')
					}
					see_data({token:this.token,id:this.id}).then(res=>{
						console.log(res)
						if(res.data.code==-1){
							this.$u.toast(res.data.data)
						}
						this.data = res.data.data
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			// 订阅专业
			cellectMajor() {
				let states = 1
				this.data.colmaj == null ? states=1 : states=0
				chartcol({token:this.token, id:this.data.major_id, state:states, type:'major'}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}
					if(states==1){
						this.$u.toast('收藏成功')
					}else{
						this.$u.toast('取消收藏成功')
					}
					see_data({token:this.token,id:this.id}).then(res=>{
						console.log(res)
						if(res.data.code==-1){
							this.$u.toast(res.data.data)
						}
						this.data = res.data.data
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			// 去报错
			toError() {
				uni.navigateTo({
					url:'/pages/index/reportErrors?item_id='+this.id
				})
			},
			// 去链接
			toLink() {
				uni.navigateTo({
					url:'/pages/index/link?id='+this.id
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 2vh 0;
		min-height: 96vh;
		background-color: #f7f6fb;
		.content{
			margin: 0 5%;
			border-radius: 20rpx;
			padding: 30rpx;
			background-color: #fff;
			margin-bottom: 80rpx;
			.produce{
				display: flex;
				flex-wrap: wrap;
				view{
					margin-right: 50rpx;
					font-size: 28rpx;
					line-height: 58rpx;
				}
				.loction{
					width: 100%;
				}
				.link{
					color: #0089fd;
				}
			}
			.title{
				margin-top: 50rpx;
				font-size: 34rpx;
				font-weight: 600;
				text-align: center;
			}
			.moudle{
				width: 100%;
				overflow: scroll;
			}
			.table{
				margin: 20rpx 0;
				.tD{
					text-align: center;
					line-height: 68rpx;
					font-size: 26rpx;
					border: 1rpx solid #eee;
					margin: 0 -1rpx -1rpx 0;
				}
				.tHead{
					display: flex;
					.tD{
						width: 50%;
						font-size: 30rpx;
						background-color: #f8f7fc;
					}
					.tD:first-child{
						border-top-left-radius: 20rpx;
					}
					.tD:last-child{
						border-top-right-radius: 20rpx;
					}
				}
				.tBody{
					display: flex;
					.tBodyLeft{
						flex: 6;
					}
					.tBodyRight{
						flex: 6;
						.tD{
							color: #028bff;
						}
					}
				}
			}
		}
		.bottom{
			background-color: #fff;
			width: 90%;
			padding: 10rpx 5%;
			position: fixed;
			bottom: 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-top: 1rpx solid #eee;
			.erroIcon{
				margin-right: 30rpx;
				font-size: 24rpx;
				padding-top: 40rpx;
				background-image: url(../../static/index/erro_icon.png);
				background-repeat: no-repeat;
				background-position: center top;
				background-size: 30rpx;
			}
			.collectModlue{
				display: flex;
				flex: 6;
				justify-content: space-between;
				font-size: 28rpx;
				image{
					width: 40rpx;
					margin-right: 12rpx;
				}
				.school{
					flex: 6;
					line-height: 68rpx;
					color: #fff;
					border-radius: 50rpx;
					background-color: #b5b5b5;
					display: flex;
					justify-content: center;
					align-items: center;
					margin-right: 20rpx;
				}
				.major{
					flex: 6;
					line-height: 68rpx;
					color: #fff;
					border-radius: 50rpx;
					background-color: #b5b5b5;
					display: flex;
					justify-content: center;
					align-items: center;
				}
				.cellected{
					background-color: #fa4a3e;
				}
			}
		}
		.cellectNone{
			justify-content:center !important;
		}
	}
</style>